body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.nav-link{
  margin-left: 40px;
}
.footer-link {
  text-decoration: none;
  color: #6c757d;   /* muted/gray color */
  margin-bottom: 2px;
  display: block;   /* allows margin-bottom to work cleanly */
}

.footer-link:hover {
  color: #343a40;   /* darker on hover (optional) */
}
#supportHero{
  background-color:rgb(56, 126, 209);
  color: white;
}
#supportHero a{
  color: white;
}
#supportWrapper{
  display: flex;
  justify-content: space-between;
  margin: 0 150px;
}

body {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
  background-color: #f9f9f9;
}

.signup-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100vh;
  padding: 0 5%;
  box-sizing: border-box;
}

.signup-left {
  width: 50%;
  padding: 20px;
}

.signup-image {
  width: 80%;
  height: auto;
  margin-bottom: 20px;
}

.signup-left h3 {
  margin-bottom: 10px;
}

.signup-left ul {
  list-style-type: disc;
  margin-left: 20px;
}

.signup-right {
  width: 50%;
  padding: 20px;
}

.signup-form {
  display: flex;
  flex-direction: column;
}

.signup-input {
  margin-bottom: 10px;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.signup-button {
  padding: 10px;
  border: none;
  border-radius: 5px;
  background-color: #007bff;
  color: white;
  cursor: pointer;
}

.signup-button:hover {
  background-color: #0056b3;
}

.nri-link {
  display: block;
  margin-top: 10px;
  color: #007bff;
  text-decoration: none;
}

.nri-link:hover {
  text-decoration: underline;
}

.disclaimer {
  margin-top: 20px;
  font-size: 12px;
  color: #666;
}
/* Login */

body {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
  background-color: #f9f9f9;
}

.login-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  padding: 0 5%;
  box-sizing: border-box;
}

.login-left {
  width: 50%;
  padding: 20px;
}

.login-image {
  width: 100%;
  height: auto;
  margin-bottom: 20px;
}

.login-right {
  width: 50%;
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.login-form {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 400px;
}

.login-input {
  margin-bottom: 10px;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.login-button {
  padding: 10px;
  border: none;
  border-radius: 5px;
  background-color: #007bff;
  color: white;
  cursor: pointer;
}

.login-button:hover {
  background-color: #0056b3;
}

.additional-info {
  margin-top: 20px;
  text-align: center;
  font-size: 12px;
  color: #666;
}